.stopwatch {
  box-sizing: border-box;
  background-color: bisque;
  border: 10px dotted #2f4f4f;
  border-radius: 80vmin;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hour-hand {
  transform-origin: bottom;
  background-color: #4682b4;
  border-radius: 40px;
  width: 20px;
  height: 25%;
  animation: 60s linear infinite move;
  position: absolute;
  bottom: 50%;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #6aabe0;
  border-radius: 40px;
  width: 10px;
  height: 50%;
  animation: 5s linear infinite move;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center {
  background-color: #d9bd1b;
  border-radius: 40px;
  width: 40px;
  height: 40px;
  position: absolute;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

